<template>

    <div class="container">
        <div class="row">
            <div class="col-md-4">
                <p id="pagination" v-if="venues.length">Showing {{ showingFrom }} - {{ showingTo }} out of {{ total }} venues
                    <button :disabled="!hasPreviousPages" v-on:click="onPreviousPage"><font-awesome-icon icon="arrow-left" /></button>
                    <button :disabled="!hasNextPages" v-on:click="onNextPage"><font-awesome-icon icon="arrow-right" /></button>
                </p>
            </div>
        </div>
        <div class="row">
            <div class="col-md-4">
                <div id="venue-list">
                    <ul class="list-unstyled">
                      <li v-for="(venue, index) in venues" class="block-content" @mouseenter="onMouseEnterVenueItem(index)" @mouseleave="onMouseLeaveVenueItem">
                        <h4>{{ venue.name }}</h4>
                        <p v-if="venue.address" class="venue-details">
                            <font-awesome-icon icon="map-marker-alt" class="blue" />
                            <span>{{ venue.address }}</span>
                        </p>
                        <p v-if="venue.telephone" class="venue-details">
                            <font-awesome-icon icon="phone" class="blue" />
                            <span>{{ venue.telephone }}</span>
                        </p>
                        <p class="text-right"><a :href="getVenueLink(venue)" target="_blank" title="More about this venue" class="icon-link purple">
                        More about this venue <font-awesome-icon icon="arrow-right" size="xs" /></a></p>
                      </li>
                    </ul>
                    <div v-if="!venues.length">
                        <p>Sorry, we couldn't find any venues matching your search criteria.</p>
                        <p>Please try again.</p>
                    </div>
                </div>
            </div>
            <div class="col-md-8 text-center" id="map-wrapper">
                <div id="map"></div>
                <button class="btn btn-white btn-square" v-on:click="onSearchThisArea">Search this area</button>
            </div>
        </div>
    </div>



</template>

<script>
import "leaflet"

const L = window.L;
const axios = require("axios");

import postcodes from '../postcodes';
const postcodeOutcodes = Object.keys(postcodes);

export default {
  data() {
    return {
      map: [],
      markers: null,
      total: null,
      defaultCentroid: ["51.4942", "-0.1747"],
      cookie_name: "sl-location",
      venues: [],
      defaultIcon: L.icon({
          iconUrl: require("../assets/img/marker.png"),
          iconSize: [33, 43],
          iconAnchor: [22, 50],
          popupAnchor: [-6, -50]
      }),
      activeIcon: L.icon({
          iconUrl: require("../assets/img/activeMarker.png"),
          iconSize: [33, 43],
          iconAnchor: [22, 50],
          popupAnchor: [-6, -50]
      }),
      numPerPage: 50,
      page: 1,
      resizeMapToMarkers: true,
      searchQueryObj: null,
      postcodeAreaCircle: null
    }
  },
  computed: {
    queryString(){
        return this.$store.state.queryString
    },
    showingFrom() {
      return ((this.numPerPage * this.page) - this.numPerPage) + 1;
    },
    showingTo() {
      if(this.numPerPage * this.page > this.total){
        return this.total
      }else{
        return this.numPerPage * this.page;
      }
    },
    hasPreviousPages(){
        return this.page > 1
    },
    hasNextPages(){
        return this.numPerPage * this.page < this.total
    },

  },
  watch: {
    venues(val) {
      this.addMarkers();
    },
    page(val) {
        this.updatePage();
    },
    queryString(val) {
        this.onSearchUpdate();
    },
  },
  methods: {

    searchVenues(searchOptions){
        this.buildSearchQueryObj(searchOptions);
        this.runSearch();
    },

    buildSearchQueryObj(searchOptions={}){

        this.resizeMapToMarkers = true;

        this.page = 1;

        this.searchQueryObj = {
            'size': this.numPerPage
        }

        let centroid = {};
        const isCentroidSearch = 'centroid' in searchOptions
        // We want to use the actual value in the input form, so if a user
        // changes it and clicks "Search this area" the correct results are returned
        let realQueryString = $('#header-search').val()
        const postalOutcode = this.getPostalOutcode(realQueryString);

        if (postalOutcode && postalOutcode in postcodes){
            centroid = {
                lat: postcodes[postalOutcode][0],
                lng: postcodes[postalOutcode][1]
            }
            realQueryString = null;

            const averageDistance = this.getPostcodeAverageNeightbourDistance(postalOutcode);
            let r;
            if(averageDistance < 0.07){
                r = 5500
            }else{
                r = 10000
            }
            if(this.postcodeAreaCircle){
                this.map.removeLayer(this.postcodeAreaCircle);
            }

            this.postcodeAreaCircle = L.circle([centroid.lat, centroid.lng], {
                color: null,
                fillColor: null,
                fillOpacity: 0,
                radius: r
            }).addTo(this.map);
            searchOptions['bounds'] = this.postcodeAreaCircle.getBounds();
        }else if (isCentroidSearch){
            console.log('Centroid search');
            centroid = {
                lat: searchOptions.centroid[0],
                lng: searchOptions.centroid[1]
            }
        }else{
            console.log('Getting map centre');
            centroid = this.map.getCenter()
        }

        this.searchQueryObj['query'] = {
          "function_score": {
            "query": {
              "bool": {}
            },
            "functions": [
              {
                "gauss": {
                    "location": {
                      "origin": { "lat": centroid.lat, "lon": centroid.lng },
                      "scale":  "50km"
                    }
                }
              },
            ],
            "score_mode": "sum"
          }
        }

        if ('bounds' in searchOptions){
            // this.resizeMapToMarkers = false;
            this.searchQueryObj['query']['function_score']['query']['bool']['filter'] = {
                "geo_bounding_box" : {
                    "location" : {
                        "top_left" : {
                            "lat" : searchOptions['bounds'].getNorth(),
                            "lon" : searchOptions['bounds'].getWest()
                        },
                        "bottom_right" : {
                            "lat" : searchOptions['bounds'].getSouth(),
                            "lon" : searchOptions['bounds'].getEast()
                        }
                    }
                }
            }
        }

        if(realQueryString){
            this.searchQueryObj['query']['function_score']['query']['bool']['must'] = {
                "multi_match" : {
                      "query": realQueryString,
                      "fields": [ "name^5", "address" ],
                      "type":     "most_fields"
                    }
            }
        }

        if(!this.searchQueryObj['query']['function_score']['query']['bool']){
            this.searchQueryObj['query']['function_score']['query']['bool'] = {
                "must": {
                    "match_all": {}
                },
            }
        }

    },

    getPostcodeAverageNeightbourDistance(postalOutcode){
        const outcodeIndex = postcodeOutcodes.indexOf(postalOutcode)
        // Loop through all of the similar postcodes e.g. BN1, BN2, BN3
        // Either side of the selected postcode
        // To build a bounding box for the search parameters
        const areaCode = this.getPostalAreaCode(postalOutcode);
        let siblingOutcode = null, siblingAreaCode = null;
        let offsetRange = 2;
        let a = null;
        let b = null;
        let totalDistance = 0
        let distanceCount = 1
        for(let i=-offsetRange;i<=offsetRange;i+=1){
            // Skip zero
            if(i){
                siblingOutcode = postcodeOutcodes[outcodeIndex + i];
                if (siblingOutcode){
                    // getPostalAreaCode
                    siblingAreaCode = this.getPostalAreaCode(siblingOutcode);
                    if (siblingAreaCode == areaCode){
                        a = postcodes[postalOutcode][0] - postcodes[siblingOutcode][0]
                        b = postcodes[postalOutcode][1] - postcodes[siblingOutcode][1]
                        totalDistance += Math.sqrt( a*a + b*b );
                        distanceCount++;
                    }
                }

            }

        }

        return totalDistance / distanceCount;

    },

    getPostalAreaCode(outcode){
        // Extract the text part of
        return outcode.replace(/[0-9]+/g,'');

    },

    runSearch(){
        var vm = this
        axios.get('http://sciencelive.net/venues-search', {
          params: {
            source: JSON.stringify(this.searchQueryObj),
            source_content_type: 'application/json'
          }
        })
          .then(function (response) {
            // handle success
            vm.total = response.data.hits.total;
            vm.venues= response.data.hits.hits.map(function(hit) {return hit._source;});
          })
          .catch(function (error) {
            // handle error
            console.log(error);
          })
          .then(function () {
            // always executed
          });
    },

    updatePage(){
        this.searchQueryObj['from'] = this.showingFrom;
        this.runSearch();
    },

    addMarkers() {
      if(!this.map) return;
      this.removeMarkers();
      const map = this.map
      const markers = L.featureGroup();
      this.venues.forEach( (venue) => {
        let marker = L.marker([venue.location.lat, venue.location.lon],  {icon: this.defaultIcon})
            .bindPopup(this.popUp(venue))
        markers.addLayer(marker)
        // .on('click', (el) => alert(el.target))
      })
      map.addLayer(markers)
      if(this.resizeMapToMarkers && this.venues.length){
        map.fitBounds(markers.getBounds());
      }
      this.markers = markers
    },
    popUp(venue){
        return '<strong>' + venue.name + '</strong><br />' + venue.address + '<br /><a href="' + this.getVenueLink(venue) + '" target="_blank" class="icon-link purple">More info</a>'
    },
    removeMarkers() {
        if(this.markers){
            this.map.removeLayer(this.markers);
            this.markers = null;
        }
    },
    getVenueLink(venue) {
        if (venue.url){
            return this.ensureHttp(venue.url);
        }else{
            return 'https://www.google.co.uk/search?q=' + venue.name + ' ' + venue.address
        }
    },

    ensureHttp(url) {
       if (!/^(f|ht)tps?:\/\//i.test(url)) {
          url = "http://" + url;
       }
       return url;
    },

    getPostalOutcode(qs){
        const matches = qs.toLowerCase().match(/^([a-z]{1,2}[0-9]{1,2}?)\s?[0-9]?[A-Z]+$|^([a-z]{1,2}[0-9]{1,2}$)/i)
        if (matches){
            return matches.slice(1).filter(n => n)[0]
        }
        return null
    },
    getPostalAreaCode(outcode){
        // Extract the text part of
        return outcode.replace(/[0-9]+/g,'');

    },

    addMap(location){
        const map = L.map('map').setView(this.defaultCentroid, 5)

        // And add map tiles
        L.tileLayer('https://api.tiles.mapbox.com/v4/{id}/{z}/{x}/{y}.png?access_token={accessToken}', {
            id: 'mapbox.streets',
            accessToken: 'pk.eyJ1IjoiYmVuIiwiYSI6ImNqN3V5Y2dzaTBmOXYzM280dmcyd2VmZHcifQ.GhV7tnV9i5DhOFcZ4Vge_g',
            attribution: '',
            minZoom: 2,
            maxZoom: 18
        }).addTo(map);

        this.map = map

    },
    searchByUserLocation(){
        const cookie_location = this.$cookie.get(this.cookie_name);
        if (cookie_location){
            this.searchVenues({centroid: cookie_location.split(',')})
        }else{
            this.getUserLocation();
        }
    },
    getUserLocation() {
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(this.getLocationSuccess, this.getLocationFailure);
      }else{
        this.searchVenues()
      }
    },
    getLocationSuccess(location) {
        if (location && location.coords){
            const centroid = [location.coords.latitude, location.coords.longitude]
            this.$cookie.set(this.cookie_name, centroid, 7);
            this.searchVenues({centroid: centroid})
        }else{
            this.searchVenues()
        }
    },
    getLocationFailure(error) {
        this.searchVenues()
    },
    onSearchUpdate () {
      this.searchVenues();
    },
    onSearchThisArea(e){
        this.searchVenues({bounds: this.map.getBounds()})
    },
    onPreviousPage(){
        if(this.page > 0){
            this.page -= 1
        }
    },
    onNextPage(){
        this.page += 1
    },
    onMouseEnterVenueItem(index){
        const i = Object.keys(this.markers._layers)[index]
        this.markers._layers[i].setIcon(this.activeIcon);
        this.markers._layers[i]._bringToFront()
    },
    onMouseLeaveVenueItem(event){
        for (var i in this.markers._layers) {
            this.markers._layers[i].setIcon(this.defaultIcon);
        }
        event.stopPropagation()
    },
  },
  mounted(){
    this.addMap();
    if(this.queryString){
        console.log('Query string: ' + this.queryString)
        this.searchVenues();
    }else{
        this.searchByUserLocation(this.q);
    }
  }
};
</script>

